body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.inst {
  font-size: .5em;
}

.scoreStyle {
  font-size: 1em;
}
.card {
  background: #fff;
  border-radius: 2px;
  height: 150px;
  margin: 1rem;
  position: relative;
  width: 250px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
  border-radius: 15px;
}

.card > .img-container:hover {
  box-shadow: 0 5px 20px yellow, 0 5px 20px yellow;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background-image: url("https://wallpapertag.com/wallpaper/full/e/3/0/128042-large-dbz-background-1920x1080-download-free.jpg");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  border-radius: 15px;
}
.card > .img-container > img {
  width: 60%
}
  
.wrapper {
  padding-top: 50px;
  background-image: url("https://images7.alphacoders.com/677/thumb-1920-677266.png");
  background-size: 100% 100%;
  height: 100vh;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
  font-family: 'Rock Salt', cursive;
  }
  
.title {
    width: 100%;
    text-align: center;
    font-size: 3rem;
    color: black;
    margin-bottom: 50px;
  }
  
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



